import data from "data.json";
import { HorizontalScroll } from "@vkontakte/vkui";
import StylesItem from "./StylesItem";
import { GenerateContext, MainContext } from "components/shared/providers";
import { useContext } from "react";
function StyleSelectionBody({ error }) {
  const { chosenStyles, setChosenStyles } = useContext(GenerateContext);

  return (
    <div className={`styleSelection__body`}>
      {data.map((category, categoryIndex) => {
        return (
          <div
            className={`styleategory__wrap ${
              error &&
              category.title == "genre" &&
              "animate__animated animate__shakeX"
            }`}
            key={category.title}
          >
            <div className="styleategory__title">
              <span>{category.name}</span>
            </div>
            <HorizontalScroll
              showArrows
              getScrollToLeft={(i) => i - 220}
              getScrollToRight={(i) => i + 220}
              color="#b0e822"
            >
              <div className="styleategory__row">
                {category.array.map((style, styleIndex) => {
                  return (
                    <StylesItem
                      key={styleIndex}
                      style={style}
                      category={category.title}
                      setChosenStyles={setChosenStyles}
                      chosenStyles={chosenStyles}
                    />
                  );
                })}
              </div>
            </HorizontalScroll>
          </div>
        );
      })}
    </div>
  );
}

export default StyleSelectionBody;
